// app/page.js or src/app/page.js

"use client";

import { useSpring, animated } from "@react-spring/web";
import { useEffect, useRef, useState } from "react";
import Link from "next/link";
import HeroSection from "../components/HeroSection";
import AboutSection from "../components/AboutSection";
import ProjectsSection from "../components/ProjectsSection";
import Magnet from "../ui/magnet";
import { useActiveSection } from "../hooks/useActiveSection"; // 👈 IMPORT THE HOOK

export default function Home() {
  const [scrollY, setScrollY] = useState(0);

  // --- 1. DEFINE REFS FOR EACH SECTION ---
  const homeRef = useRef(null);
  const aboutRef = useRef(null);
  const projectsRef = useRef(null); // Assuming you'll add this section
  const contactRef = useRef(null); // Assuming you'll add this section

  // Group the refs for the hook
  const sectionRefs = {
    home: homeRef,
    about: aboutRef,
    projects: projectsRef,
    contact: contactRef,
  };

  // --- 2. USE THE HOOK TO DETERMINE ACTIVE SECTION ---
  const activeSection = useActiveSection(sectionRefs); // 👈 GET ACTIVE SECTION

  // Burger Menu State and Handlers
  const [mounted, setMounted] = useState(false);
  const [showBurger, setShowBurger] = useState(false);
  const [menuOpen, setMenuOpen] = useState(false);
  const [hasToggled, setHasToggled] = useState(false);
  const [hasPopped, setHasPopped] = useState(false);
  // activeSection is now managed by the hook
  const [hoveredLink, setHoveredLink] = useState(null);
  const [closestMagnet, setClosestMagnet] = useState(null); 

  const mobileNavItems = [
    { id: "home", label: "Home", href: "#home", delay: "0.12s" },
    { id: "about", label: "About", href: "#about", delay: "0.15s" },
    { id: "projects", label: "Projects", href: "#projects", delay: "0.18s" },
    { id: "contact", label: "Contact", href: "#contact", delay: "0.21s" },
  ];

  const toggleMenu = () => {
    setHasToggled(true);
    setMenuOpen((prev) => !prev);
  };

  // Function now uses the activeSection value from the hook
  const shouldShowBullet = (sectionId) => {
    if (hoveredLink) return hoveredLink === sectionId;
    return activeSection === sectionId;
  };

  // Effect to manage the pop animation
  useEffect(() => {
    if (showBurger || menuOpen) setHasPopped(true);
  }, [showBurger, menuOpen]);

  // Effect to handle scroll and burger visibility
  useEffect(() => {
    setMounted(true);
    const handleScroll = () => {
      const currentScrollY = window.scrollY;
      // Show burger after scrolling down 50px
      setShowBurger(currentScrollY > 50); 
      setScrollY(currentScrollY);
    };

    window.addEventListener("scroll", handleScroll, { passive: true });
    return () => window.removeEventListener("scroll", handleScroll);
  }, []);
  
  // Effect to disable body scroll when menu is open
  useEffect(() => {
    document.body.style.overflow = menuOpen ? "hidden" : "";
    return () => { document.body.style.overflow = ""; };
  }, [menuOpen]);


  const heroParallax = useSpring({
    y: scrollY * -0.2,
    config: { tension: 120, friction: 20 },
  });

  const aboutParallax = useSpring({
    y: scrollY * -0.1,
    config: { tension: 120, friction: 20 },
  });

  return (
    <main className="scroll-smooth">
      
      {/* 💥 STICKY BURGER (ICON) - PLACED HERE WITH HIGHEST Z-INDEX 💥 */}
      {mounted && (
        <div className={`fixed top-5 right-5 z-[100] ${(showBurger || menuOpen)
            ? "animate-pop pointer-events-auto"
            : hasPopped
              ? "animate-depop pointer-events-none"
              : "opacity-0 pointer-events-none"
          } md:top-8 md:right-8`}> 
          <Magnet padding={50} magnetStrength={10}>
            <div
              className="relative w-14 h-14 md:w-20 md:h-20 cursor-pointer flex justify-center items-center rounded-full overflow-hidden group bg-[#333333]"
              onClick={toggleMenu}
            >
              <div
                className={`absolute bottom-0 left-0 w-full bg-blue-600 transition-all duration-500 ease-in-out z-0 
                ${menuOpen ? "h-full" : "h-0 group-hover:h-full"}`}
              />
              <span
                className="absolute w-8 h-0.5 bg-white rounded transition-transform duration-300 z-10"
                style={{
                  transform: menuOpen ? "rotate(45deg)" : "translateY(-3px)",
                }}
              ></span>
              <span
                className="absolute w-8 h-0.5 bg-white rounded transition-transform duration-300 z-10"
                style={{
                  transform: menuOpen ? "rotate(-45deg)" : "translateY(3px)",
                }}
              ></span>
            </div>
          </Magnet>
        </div>
      )}
      {/* ---------------------------------------------------- */}

      {/* 🚢 SIDE MENU PANEL - ACTIVE SECTION LOGIC APPLIED HERE 🚢 */}
      {mounted && (
        <div
          className={`menuPanel fixed top-0 right-0 h-full flex flex-col px-6 z-50 w-full md:w-1/3 ${menuOpen
              ? "openWave"
              : hasToggled
                ? "closeWave"
                : "translate-x-[100%] pointer-events-none"
            }`}
          style={{ backgroundColor: "#333333" }}
        >
          <div className="mt-32 mb-4">
            <p className={`${menuOpen ? "textIn" : "textOut"} text-gray-400 text-sm uppercase tracking-wider`} style={{ animationDelay: "0.05s" }}>
              Navigation
            </p>
            <hr className={`${menuOpen ? "textIn" : "textOut"} border-gray-700 mt-1`} style={{ animationDelay: "0.08s" }} />
          </div>

          <div className="flex flex-col space-y-2 pl-6">
            {mobileNavItems.map((item) => (
              <Link
                key={item.id}
                href={item.href}
                className={`group flex items-center ${menuOpen ? "textIn" : "textOut"}`}
                style={{ animationDelay: item.delay }}
                onMouseEnter={() => setHoveredLink(item.id)}
                onMouseLeave={() => setHoveredLink(null)}
                onClick={toggleMenu} // Close menu on click
              >
                {/* 🎯 BULLET LOGIC: shouldShowBullet uses the activeSection from the hook 🎯 */}
                <span className={`mr-4 h-3 w-3 rounded-full bg-white transition-all duration-300 ease-in-out ${shouldShowBullet(item.id) ? "opacity-100 scale-100" : "opacity-0 scale-0"}`} />
                <span className="text-white text-5xl font-normal py-2">{item.label}</span>
              </Link>
            ))}
          </div>
        </div>
      )}
      {/* ---------------------------------------------------- */}
      
      {/* 1. HERO SECTION CONTAINER (HOME) */}
      <div 
        className="relative z-10 overflow-hidden h-[115vh] w-full"
        ref={homeRef} // 👈 ATTACH REF
        id="home" // 👈 ADD ID for the Intersection Observer
      >
        <HeroSection 
          heroStyle={heroParallax} 
          menuOpen={menuOpen}
          toggleMenu={toggleMenu}
          mobileNavItems={mobileNavItems} 
          // Removed unused setActiveSection prop
          closestMagnet={closestMagnet}
          setClosestMagnet={setClosestMagnet}
          setHoveredLink={setHoveredLink}
          hoveredLink={hoveredLink}
          shouldShowBullet={shouldShowBullet}
        /> 
      </div>
    
      
      {/* 2. ABOUT SECTION CONTAINER */}
      <animated.div 
        style={aboutParallax}
        className="relative z-30"
        ref={aboutRef} // 👈 ATTACH REF
        id="about" // 👈 ADD ID for the Intersection Observer
      >
        <AboutSection />
      </animated.div>
      
      {/* 3. PROJECTS SECTION CONTAINER - Placeholder to ensure the bullet works */}
      {/* You'll need to create the ProjectsSection component and adjust its height */}
      <div
        className="h-[100vh] bg-gray-900 flex justify-center items-center"
        ref={projectsRef} // 👈 ATTACH REF
        id="projects" // 👈 ADD ID
      >
        <ProjectsSection /> 
      </div>
      
      {/* 4. CONTACT SECTION CONTAINER - Placeholder */}
      <div
        className="h-[100vh] bg-black flex justify-center items-center"
        ref={contactRef} // 👈 ATTACH REF
        id="contact" // 👈 ADD ID
      >
        {/* Contact Content Here */}
        <h2 className="text-white text-6xl">Contact Section</h2>
      </div>

    </main>
  );
}