// src/hooks/useActiveSection.js

import { useState, useEffect, RefObject } from "react";

// Define the interface for the object of refs.
// It maps string keys (section IDs) to a React RefObject holding an HTMLElement.
interface SectionRefs {
  [key: string]: RefObject<HTMLElement>;
}

/**
 * Custom hook to determine the currently visible section using IntersectionObserver.
 * It tracks which section is in the middle of the viewport and updates the activeSection state.
 * * @param {SectionRefs} sectionRefs - An object containing React refs for each section.
 * @returns {string} The ID of the currently active section (default is "home").
 */
export const useActiveSection = (sectionRefs: SectionRefs): string => {
  const [activeSection, setActiveSection] = useState("home");
  
  useEffect(() => {
    // Basic check for client-side environment and IntersectionObserver support
    if (typeof window === 'undefined' || !('IntersectionObserver' in window)) {
        return;
    }

    const observer = new IntersectionObserver(
      (entries) => {
        entries.forEach((entry) => {
          if (entry.isIntersecting) {
            // Set the active section to the ID of the intersecting element
            setActiveSection(entry.target.id);
          }
        });
      },
      {
        root: null, // viewport
        // The rootMargin is set to make the section active only when it's near the center
        // -50% from the bottom effectively makes the center 50% line the trigger.
        rootMargin: "0px 0px -50% 0px", 
        threshold: 0,
      }
    );

    // Observe each section reference
    Object.values(sectionRefs).forEach((ref) => {
      if (ref.current) {
        observer.observe(ref.current);
      }
    });

    return () => {
      // Clean up the observer when the component unmounts
      Object.values(sectionRefs).forEach((ref) => {
        if (ref.current) {
          observer.unobserve(ref.current);
        }
      });
      observer.disconnect();
    };
  }, [sectionRefs]);

  return activeSection;
};